<template>
  <div class="contianer">
    <h3 class="title">企业名称：{{comName}}-{{name}}</h3>
    <div class="input-box">
        <label class="label">所属商品计划:</label>
            <el-select v-model="value" placeholder="无">
              <el-option
                v-for="item in companys"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
      </div>
      <div class="input-box">
        <label class="label">商品名称：</label>
        <el-input class="com-input"
          placeholder="请输入商品名称"
          v-model="goodsName">
        </el-input>
      </div>
      <div class="input-box">
        <label class="label">商品简介：</label>
        <el-input
          type="textarea"
          :rows="2"
          size="medium"
          placeholder="请输入内容"
          v-model="textarea">
        </el-input>
      </div>
      <div class="input-box">
        <span></span>
        <label for="" class="label">商品生效日期：</label>
        <el-input class="com-input"
          placeholder=""
          suffix-icon="el-icon-date"
          v-model="availTime">
        </el-input>
        <span></span>
        <label for="" class="label label-left">商品失效日期：</label>
        <el-input class="com-input"
          placeholder=""
          suffix-icon="el-icon-date"
          v-model="expiryTime">
        </el-input>
      </div>
      <div>
        <p>商品绑定优加服务：</p>
        <el-checkbox-group v-model="checkedServes" @change="handleCheckedServesChange">
          <el-checkbox v-for="serve in servesList" :label="serve" :key="serve">{{serve}}</el-checkbox>
        </el-checkbox-group>
      </div>
  </div>
</template>

<script>
import { companys } from "../../data/comList";
export default {
  name: "addOrEditGoods",
  data() {
    return {
      comName: "中国平安",
      name: this.$route.query.type === "edit" ? "健康想看就看" : "新增商品",
      companys,
      value: "",
      goodsName: "",
      textarea: "",
      checkedServes: [],
      availTime: '',
      expiryTime: '',
      servesList: [
        "押金垫付",
        "电话医生",
        "体检服务",
        "门诊绿通",
        "住院绿通",
        "手术绿通",
        "二次诊疗"
      ]
    };
  },
  methods: {
    handleCheckedServesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.cities.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length;
    }
  }
};
</script>

<style>
.contianer {
  padding-left: 10px;
  text-align: left;
}
.title {
  border-bottom: 1px solid #eee;
  margin-bottom: 20px;
  padding-bottom: 5px;
}
.com-input {
  width: 210px;
}
.input-box {
  display: flex;
  align-items: flex-start;
  margin-bottom: 10px;
}
.label{
  width: 120px;
}
.label-left{
  margin-left: 15px;
}
.input-width{
  width: 216px;
}
.el-textarea{
  width:300px;
}
</style>
